import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, AlertController } from 'ionic-angular';
import { SelectCommunityNamePage } from "../select-community-name/select-community-name";
import { SelectBlockNoPage } from "../select-block-no/select-block-no";
import { SelectUnitNoPage } from "../select-unit-no/select-unit-no";
import { SelectFloorNoPage } from "../select-floor-no/select-floor-no";
import { SaleSubmitPage } from "../sale-submit/sale-submit";
/**
 * Generated class for the SalesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sales',
  templateUrl: 'sales.html',
})
export class SalesPage {
  items = [
    { index: 0, name: '', pageName: SelectCommunityNamePage, edited: false },
    { index: 1, name: '', pageName: SelectBlockNoPage, edited: false },
    { index: 2, name: '', pageName: SelectUnitNoPage, edited: false },
    { index: 3, name: '', pageName: SelectFloorNoPage, edited: false }
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, private alertCtrl: AlertController) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SalesPage');
  }

  itemSelected(item) {
    console.log("Selected Item", item);

    if (item.index != 0 && this.items[item.index - 1].edited == false) {
      this.presentAlert('' + this.items[item.index - 1].name);
    } else {
      let modal = this.modalCtrl.create(item.pageName);
      modal.onDidDismiss(data => {
        if (data) {
          console.log("SalesPage pageName = ", item.name, "data = ", data);
          if (item.name != data && item.edited) {
            if (item.index == 0) {
              this.items[1].edited = false;
              this.items[1].name = '';
              this.items[2].edited = false;
              this.items[2].name = '';
              this.items[3].edited = false;
              this.items[3].name = '';
            } else if (item.index == 1) {
              this.items[2].edited = false;
              this.items[2].name = '';
              this.items[3].edited = false;
              this.items[3].name = '';
            } else if (item.index == 2) {
              this.items[3].edited = false;
              this.items[3].name = '';
            }
          }
          item.name = data;
          item.edited = true;
        }
      });
      modal.present();
    }

  }

  nextPage() {
    if (this.items[0].edited && this.items[1].edited && this.items[2].edited && this.items[3].edited) {
      this.navCtrl.push(SaleSubmitPage, { 'community': this.items[0].name, 'block': this.items[1].name, 'unit': this.items[2].name, 'floor': this.items[3].name });
    } else {
      this.presentAlert('');
    }
  }

  presentAlert(title: string, subTitle: string = '') {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['']
    });
    alert.present();
  }

}


