import { Component } from '@angular/core';
import { IonicPage, ViewController, NavParams } from 'ionic-angular';

/**
 * Generated class for the SelectHourseSizePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-hourse-size',
  templateUrl: 'select-hourse-size.html',
})
export class SelectHourseSizePage {
  type;
  shiSelect = [
    { value: 1, name: '', checked: false },
    { value: 2, name: '', checked: false },
    { value: 3, name: '', checked: false },
    { value: 4, name: '', checked: false },
    { value: 5, name: '', checked: false },
    { value: 6, name: '', checked: false }
  ];
  constructor(public viewCtrl: ViewController, public navParams: NavParams) {
    this.type = navParams.get('type');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectHourseSizePage');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  ok() {
    this.viewCtrl.dismiss(this.shiSelect);
  }
}



