  ionViewDidLoad() {
    console.log('ionViewDidLoad OldhoursePage');
  }

  selectNews(page) {
    console.log('selectNews newsUrl ' + page.newsUrl);
  }

  scrollTop() {
    console.log('scrollTop');
    this.content.scrollToTop();
  }
  searchItems(ev) {
    this.initHourseList();

    var val = ev.target.value;

    if (val && val.trim() != '') {
      this.hourseList = this.hourseList.filter((item) => {
        return (item.title.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  selectArea() {
    let modal = this.modalCtrl.create(SelectAreaPage);
    modal.onDidDismiss(data => {
      if (data) {
        console.log('selectArea data = ', data);
        this.areaSelect = data.key;
      }
    });
    modal.present();
  }

  selectHoursesize() {
    let modal = this.modalCtrl.create(SelectHourseSizePage);
    modal.onDidDismiss(data => {
      if (data) {
        console.log('selectHoursesize data = ', data);
        if (typeof (data) != 'undefined') {
          let count = 0;
          data.forEach(element => {
            console.log('selectHoursesize data.name = ', element.name);
            console.log('selectHoursesize data.value = ', element.value);
            console.log('selectHoursesize data.checked = ', element.checked);

            if (element.checked) {
              count++;
              this.sizeSelect = element.name;
            }
          });
          if (count > 1) {
            this.sizeSelect = '';
          }
        }
      }
    });
    modal.present();
  }

  selectPrice() {
    let modal = this.modalCtrl.create(SelectPricePage);
    modal.onDidDismiss(data => {
      if (data) {
        console.log('selectPrice data = ', data);
        console.log('selectPrice data.name = ', data.name);
        this.priceSelect = data.name;
      }
    });
    modal.present();
  }

