import { Component } from '@angular/core';
import { IonicPage, ViewController, NavParams } from 'ionic-angular';

/**
 * Generated class for the SelectAreaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-area',
  templateUrl: 'select-area.html',
})
export class SelectAreaPage {

  typeList = ['', ''];

  ditieDic = [
    { key: '', value: [] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] }
  ];

  quyuDic = [
    { key: '', value: [] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
    { key: '', value: ['', '', '', ''] },
  ];

  yiji = [];
  erji = [];

  typeStr: string = '';
  yijiStr: string = '';
  erjiStr: string = '';

  constructor(public viewCtrl: ViewController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectAreaPage');
  }

  dismiss(param) {
    this.viewCtrl.dismiss(param ? param : '');
  }

  selectType(type) {
    console.log('selectType', type);
    if ('' == type && this.typeStr != type) {
      this.yiji = this.quyuDic;
      this.erji = [];
    } else if ('' == type && this.typeStr != type) {
      this.yiji = this.ditieDic;
      this.erji = [];
    } else {

    }

    this.typeStr = type;
  }

  selectQu(item) {
    console.log('selectQu ', item);
    if ('' == item.key) {
      this.dismiss({ 'key': this.typeStr });
    } else {
      this.yijiStr = item.key;
      this.erji = item.value;
    }
  }

  selectShangquan(item) {
    console.log('selectShangquan', item);
    this.erjiStr = item;
    if ('' == item) {
      this.dismiss({ 'key': this.yijiStr });
    } else {
      this.dismiss({ 'key': this.erjiStr });
    }
  }

}



