import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TabsPage } from "../pages/tabs/tabs";
import { MessagePage } from "../pages/message/message";
import { SchedulePage } from "../pages/schedule/schedule";
import { AboutPage } from "../pages/about/about";
import { SelectcityPage } from "../pages/selectcity/selectcity";
import { OldhoursePage } from "../pages/oldhourse/oldhourse";
import { DealPage } from "../pages/deal/deal";
import { MapPage } from "../pages/map/map";
import { CommunityPage } from "../pages/community/community";
import { EvaluatePage } from "../pages/evaluate/evaluate";
import { RentPage } from "../pages/rent/rent";
import { SalesPage } from "../pages/sales/sales";
import { SelectCommunityNamePage } from "../pages/select-community-name/select-community-name";
